.admin-login-control{
  height: 100vh;
  /* display: flex; */
  align-items: center;
  justify-content: center;
  background-color: #53565a;
  padding: 100px;

}

.admin-login-control input, button {
  appearance: none;
  background: none;
  border: none;
  outline: none;
}
  
.admin-login-control form{
  display: block;
  position: relative;
}

.admin-login-control form::after{
  content: '';
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: 1;
  background-image: linear-gradient(to bottom right, #ffce00, #fe4880);

}

.admin-login-control form .form-inner{
  position: relative;
  display: block;
  background-color: #fff;
  padding: 30px;
  z-index: 2;
  padding-bottom: 50px;
}

.admin-login-control form .form-inner h2 {
  color: #888;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}


.admin-login-control form .form-inner .form-group{
  display: block;
  width: 300px;
  margin-bottom: 15px;
}


.admin-login-control .form-inner .form-group label{
  display: block;
  color: #666666;
  font-size: 18px;
  margin-bottom: 5px;
  transition: 0.4s;
}


.admin-login-control .form-inner .form-group:focus-within label{
  color: #fe4880;
}


.admin-login-control form .form-inner .form-group input{
  display: block;
  width: 300%;
  padding: 10px 15px;
  background-color: #f8f8f8;
  border-radius: 8px;
  transition: 0.4s;
}

.admin-login-control form .form-inner .form-group input:focus{
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}


.admin-login-control form .form-inner input[type="submit"]{
  display: inline-block;
  padding: 10px 15px;
  border-radius: 8px;
  background-image: linear-gradient(to right, #ffce00 50%, #ffce00 50%, #fe4880);
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: #fff;
  font-weight: 700;
  cursor: pointer;

}

.admin-login-control form .form-inner input[type="submit"]:hover{
  background-position: 100% 0%;
}